import React, {  Component } from 'react';
import { LoadState } from '../../../utils/Enums';
import Badge from 'react-bootstrap/Badge';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Tooltip from 'react-bootstrap/Tooltip';
import { botometerUrl, accountVerificationUrl } from '../../../utils/Urls';
import { CircularProgressbar, buildStyles } from 'react-circular-progressbar';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import 'react-circular-progressbar/dist/styles.css';

export default class Botometer extends Component {
  state = {
    loadState: LoadState.LOADING,
    botometerData: null,
    verifiedAccount: false,
  };

  calculateConfidence(botometerData) {
    return ((1 - (botometerData.display_scores.english / 5)) * 100).toFixed(1);
  }

  setConfidenceDelayed(confidence) {
    setTimeout(() => this.setState({
      botometerData: {
        ...this.state.botometerData,
        confidence
      }
    }), 500);
  }

  async componentDidMount() {
    try {
      const response = await fetch(`${accountVerificationUrl}?tweetUrl=${this.props.tweetUrl}`)
      const responseJson = await response.json();
      const verified = responseJson.verified;
      if (verified) {
        this.setState({
          loadState: LoadState.LOADED,
          verifiedAccount: true,
          botometerData: {confidence: 0}
        });
        this.setConfidenceDelayed(100);
        return;
      }
    }
    catch (error) {
      console.error(error);
    }
    try {
      const response = await fetch(`${botometerUrl}?tweetUrl=${this.props.tweetUrl}`)
      const responseJson = await response.json();
      const botometerData = responseJson;
      botometerData.confidence = this.calculateConfidence(botometerData);
      this.setState({
        loadState: LoadState.LOADED,
        botometerData: {
          botometerData,
          confidence: 0
        }
      });
      this.setConfidenceDelayed(botometerData.confidence);
    }
    catch (error) {
      console.error(error);
      this.setState({
        loadState: LoadState.FAILED,
        botometerData: null
      });
    }
  }

  getAuthor() {
    const expression = /twitter.com\/([a-zA-Z0-9_]+)\//;
    const regex = new RegExp(expression);
    const result = this.props.tweetUrl.match(regex);
    if (result && result[1]) {
      return '(' + result[1] + ')';
    }
  }

  renderTooltip(props) {
    const content = this.state.verifiedAccount ? (
      <div>
        The account is verified by Twitter.
      </div>
    ) : (
      <div>
        The confidence is based on the author's Twitter activity.
        <br/><br/>
        <i>Powered by Botometer<span role="img" aria-label="trademark">Â®ï¸</span></i>
      </div>
    );
    return (
      <Tooltip id='button-tooltip' {...props}>
        {content}
      </Tooltip>
    );
  }

  renderConfidenceBar() {
    let styles = buildStyles({
      pathTransitionDuration: 2.0
    });
    if (this.state.botometerData.confidence < 50) {
      styles = buildStyles({
        pathTransitionDuration: 2.0,
        pathColor: `red`,
        textColor: 'red'
      });
    }
    return (
        <span>
            <div style={{width: 75}}>
                <CircularProgressbar
                  value={this.state.botometerData.confidence}
                  text={`${this.state.botometerData.confidence}%`}
                  styles={styles}
                />
            </div>
            <div style={{width: 30}}/>
        </span>)
  }

  render() {
    let result = '';
    if (this.state.loadState === LoadState.LOADING) {
      result = (<div>Loading author analysis...</div>);
    } else if (this.state.loadState === LoadState.FAILED) {
      result = (<div>Failed to retrieve the author analysis. Please try again later.</div>);
    } else if (this.state.loadState === LoadState.LOADED) {
      let confidence = this.state.botometerData.confidence;
      let message = ''
      if (confidence < 50) {
        message = (
          <span>
            The author of this tweet {this.getAuthor()} <b>is a bot</b> account.
          </span>
        );
        confidence = 100 - confidence;
      } else {
        message = (
          <span>
            The author of this tweet {this.getAuthor()} is <b>not</b> a bot account.
          </span>
        );
      }
      result = (
        <div>
          {message} (
          <b>{confidence}%</b> confidence) <OverlayTrigger
            placement='top'
            delay={{ show: 100, hide: 100 }}
            overlay={(props) => this.renderTooltip(props)}
          >
            <Badge variant='secondary'>?</Badge>
          </OverlayTrigger>
        </div>
      );
    }
    return (
      <Row>
        <Col>
        <h4><span role="img" aria-label="author">âœï¸</span> About Author</h4>
        {result}
        </Col>
        {this.state.loadState === LoadState.LOADED && this.renderConfidenceBar()}
      </Row>
    );
  }
}
