import React, {  Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import queryString from 'query-string';
import { TwitterTweetEmbed } from 'react-twitter-embed';
import Spinner from 'react-bootstrap/Spinner'
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from 'react-bootstrap/Button';
import IconButton from '@material-ui/core/IconButton';
import ShareIcon from '@material-ui/icons/Share';
import LinkIcon from '@material-ui/icons/Link';
import CheckIcon from '@material-ui/icons/Check';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Popover from 'react-bootstrap/Popover';

import Botometer from './Botometer';
import RelevantNews from './RelevantNews';
import Comments from './Comments';
import { getTweetId } from '../../../utils/Commons';

class TweetPage extends Component {
  state = {
    copiedShareLink: false,
  };

  constructor(props) {
    super(props);
    const paramsString = props.location.search;
    const params = queryString.parse(paramsString);
    this.tweetUrl = params.tweetUrl;
    this.tweetId = getTweetId(params.tweetUrl);
    this.showFoundMessage = (params.found === 'true');
  }

  copyLink() {
    let link = window.location.href;
    
    // Temporary
    link = link.replace("new.thoracle.site", "www.thoracle.net");

    const linkInputElement = document.createElement('textarea');
    linkInputElement.value = link;
    document.body.appendChild(linkInputElement);
    linkInputElement.select();
    document.execCommand("copy");
    linkInputElement.blur();
    document.body.removeChild(linkInputElement);
    this.setState({ copiedShareLink: true });
  }

  renderShareButton() {
    const variant = this.state.copiedShareLink ? 'success' : 'primary';
    const content = this.state.copiedShareLink ? (
      <Fragment> <CheckIcon /> Copied! </Fragment>
     ) : (
      <Fragment> <LinkIcon /> Copy Link </Fragment>
    );
    const popover = (
      <Popover>
        <Popover.Content>
          <Button variant={variant} onClick={() => this.copyLink()}>
            {content}
          </Button>
        </Popover.Content>
      </Popover>
    );
    return (
      <OverlayTrigger rootClose='false' trigger="click" placement="bottom" overlay={popover}>
        <IconButton aria-label="share">
          <ShareIcon/>
        </IconButton>
      </OverlayTrigger>
    );
  }

  renderFoundMessage() {
    if (this.showFoundMessage) {
      return (
        <h2>
          <span role="img" aria-label="found">âœ”</span> Tweet found! {this.renderShareButton()}
        </h2>
      );
    }
    return (
      <h2>
      <span role="img" aria-label="analysis">ðŸ“ƒ</span> Tweet Analysis {this.renderShareButton()}
      </h2>
    );
  }

  render() {
    return (
      <div className='content'>
        {this.renderFoundMessage()}
        <hr />
        <Row>
          <Col xs="12">
            <TwitterTweetEmbed
                tweetId={this.tweetId}
                options={{conversation: 'none'}}
                placeholder={<Spinner animation='border' role='status' size='large'/>}
            />
          </Col>
        </Row>
        <hr />
        <Botometer tweetUrl={this.tweetUrl} />
        <hr />
        <Comments pageUrl={window.location.href} disqusId={this.tweetId} />
        <hr />
        <RelevantNews tweetUrl={this.tweetUrl} />
        <hr />
      </div>
    );
  }
}

export default withRouter(TweetPage);
