import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import queryString from 'query-string';

import TweetSearch from './HomePage/TweetSearch';
import { extractUrl, getPathFromUrl } from '../../utils/Commons';

class ExternalSearchPage extends Component {
  constructor(props) {
    super(props);
    const sharedUrl = this.getSharedUrl();
    if (sharedUrl && sharedUrl.includes('twitter.com')) {
      this.tweetUrl = getPathFromUrl(sharedUrl);
    } else {
      this.imageUrl = sharedUrl;
    }
  }

  getSharedUrl() {
    const paramsString = this.props.location.search;
    const params = queryString.parse(paramsString);
    let sharedUrl;
    if (!sharedUrl && params.url) {
      const decodedParam = decodeURI(params.url);
      sharedUrl = extractUrl(decodedParam);
    }
    if (!sharedUrl && params.title) {
      const decodedParam = decodeURI(params.title);
      sharedUrl = extractUrl(decodedParam);
    }
    if (!sharedUrl && params.text) {
      const decodedParam = decodeURI(params.text);
      sharedUrl = extractUrl(decodedParam);
    }
    return sharedUrl;
  }
  
  render() {
    return (
      <div className='content content-home'>
        <TweetSearch imageUrl={this.imageUrl} tweetUrl={this.tweetUrl} />
        <br />
        <hr />
      </div>
    );
  }
}

export default withRouter(ExternalSearchPage);
