import React, {Component, Fragment} from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import Button from 'react-bootstrap/Button';
import {redirectNewTab, redirect} from '../utils/Commons';
import {extensionUrl, playStoreUrl} from '../utils/Urls';
import {isOnApp} from "../utils/Commons";
import logo from '../resources/logo128.png';
import {getSession} from '../utils/Session';
import AnalyticsService from "./firebase/analytics";
import MobileStoreButton from './MobileStoreButton';


let analyticsService = AnalyticsService.getInstance();

export default class AppNavbar extends Component {
  redirectToExtension() {
        analyticsService.logEvent(AnalyticsService.Action.extension_clicked);
        redirectNewTab(extensionUrl);
  }

  componentDidMount() {
    this.setState({ session: getSession() });
  }

  renderBrand() {
    return (
      <Navbar.Brand as='a' href='/'>
        <img alt='Thoracle Logo' src={logo} width='32' height='32' className='d-inline-block align-top'/>
        &nbsp;
        Thoracle
      </Navbar.Brand>
    );
  }

  renderExtensionButton() {
    return (
      <Nav.Item>
        <Button onClick={() => this.redirectToExtension()} variant='warning'>
          Get the Chrome extension!
        </Button>
      </Nav.Item>
    );
  }

  renderAndroidLogo() {
      if (!isOnApp()) {
          return (
              <Nav.Item>
                <MobileStoreButton url={playStoreUrl} />
              </Nav.Item>
          )
      }
  }

  renderNavLinks() {
    return (
      <Fragment>
        <Nav.Item>
          <Button variant="outline-info" onClick={() => redirect('/about')}>
            About
          </Button>
        </Nav.Item>
      </Fragment>
    );
  }

  render() {
    return (
      <Navbar fixed="top" collapseOnSelect expand='lg' bg='dark' variant='dark'>
        {this.renderBrand()}
        <Navbar.Toggle aria-controls='responsive-navbar-nav' />
        <Navbar.Collapse id='responsive-navbar-nav'>
          <Nav className='mr-auto'/>
          <Nav className='navbar-elements'>
            {this.renderNavLinks()}
            {this.renderExtensionButton()}
            {this.renderAndroidLogo()}
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    );
  }
}
