import Firebase from "./firebase";

export default class AnalyticsService{
    static instance = null;
    firebase = null;
    analytics = null;

    static Action = {
        tweet_ss_send: 0,
        image_link_send: 1,
        tweet_found: 2,
        tweet_not_found: 3,
        logout: 4, // @deprecated
        login: 5, // @deprecated
        sign_up: 6, // @deprecated
        like_comment: 7, // @deprecated
        dislike_comment: 8, // @deprecated
        send_comment: 9, // @deprecated
        send_reply: 10, // @deprecated
        like_reply: 11, // @deprecated
        dislike_reply: 12, // @deprecated
        view_comments: 13, // @deprecated
        view_journal_entries: 14, // @deprecated
        twitter_link_send: 15,
        open_comments: 16,
        close_comments: 17,
        article_clicked: 18,
        popular_tweet_clicked: 19,
        extension_clicked: 20
    };


    static getInstance(){
        if (AnalyticsService.instance == null){
            AnalyticsService.instance = new AnalyticsService();
        }
        return this.instance
    }

    constructor(){
        this.firebase = new Firebase();
        this.analytics = this.firebase.getAnalytics();
    }


    /*
     * tweet_ss_send
     * image_link_send [link]
     * tweet_found [tweet_id]
     * tweet_not_found
     * logout @deprecated
     * login @deprecated
     * sign_up @deprecated
     * like_comment [comment_id] @deprecated
     * dislike_comment [comment_id] @deprecated
     * send_comment [comment_id] @deprecated
     * send_reply [comment_id] @deprecated
     * like_reply [comment_id] @deprecated
     * dislike_reply [comment_id] @deprecated
     * view_comments [tweet_id] @deprecated
     * view_journal_entries [tweet_id] @deprecated
     * twitter_link_send {tweet_id}
     * open_comments [page_link]
     * close_comments [page_link]
     * article_clicked [article_link]
     * popular_tweet_clicked [tweet_id]
     * extension_clicked
     */
    logEvent(action, parameters) {
        switch (action) {
            case AnalyticsService.Action.tweet_ss_send:
                this.analytics.logEvent("tweet_ss_send");
                break;
            case AnalyticsService.Action.image_link_send:
                this.analytics.logEvent("tweet_link_send", {tweet_id: parameters[0]});
                break;
            case AnalyticsService.Action.tweet_found:
                this.analytics.logEvent("tweet_found", {tweet_id: parameters[0]});
                break;
            case AnalyticsService.Action.tweet_not_found:
                this.analytics.logEvent("tweet_not found");
                break;
            case AnalyticsService.Action.logout:
                this.analytics.logEvent("logout");
                break;
            case AnalyticsService.Action.login:
                this.analytics.logEvent("login");
                break;
            case AnalyticsService.Action.sign_up:
                this.analytics.logEvent("sign_up");
                break;
            case AnalyticsService.Action.like_comment:
                this.analytics.logEvent("like_comment", {post_id: parameters[0]});
                break;
            case AnalyticsService.Action.dislike_comment:
                this.analytics.logEvent("dislike_comment", {post_id: parameters[0]});
                break;
            case AnalyticsService.Action.send_comment:
                this.analytics.logEvent("send_comment", {post_id: parameters[0]});
                break;
            case AnalyticsService.Action.send_reply:
                this.analytics.logEvent("send_reply", {post_id: parameters[0]});
                break;
            case AnalyticsService.Action.like_reply:
                this.analytics.logEvent("like_reply", {post_id: parameters[0]});
                break;
            case AnalyticsService.Action.dislike_reply:
                this.analytics.logEvent("dislike_reply", {post_id: parameters[0]});
                break;
            case AnalyticsService.Action.view_comments:
                this.analytics.logEvent("view_comment", {post_id: parameters[0]});
                break;
            case AnalyticsService.Action.view_journal_entries:
                this.analytics.logEvent("view_journal_entries", {tweet_id: parameters[0]});
                break;
            case AnalyticsService.Action.twitter_link_send:
                this.analytics.logEvent("twitter_link_send", {tweet_id: parameters[0]});
                break;
            case AnalyticsService.Action.open_comments:
                this.analytics.logEvent("open_comment", {page_link: parameters[0]});
                break;
            case AnalyticsService.Action.close_comments:
                this.analytics.logEvent("close_comment", {page_link: parameters[0]});
                break;
            case AnalyticsService.Action.article_clicked:
                this.analytics.logEvent("article_clicked", {article_link: parameters[0]});
                break;
            case AnalyticsService.Action.popular_tweet_clicked:
                this.analytics.logEvent("popular_tweet_clicked", {tweet_id: parameters[0]});
                break;
            case AnalyticsService.Action.extension_clicked:
                this.analytics.logEvent("extension_clicked");
                break;
            default:
                this.analytics.logEvent("unknown_event");
                break;
        }
    }
}
