import React from 'react';
import YouTube from 'react-youtube-embed'
import {TwitterFollowButton} from "react-twitter-embed";

export default () => {
  return (
    <div className='content content-narrow'>
      <h1>About</h1>
      <p>Thoracle is a tool to search tweets from their screenshots. After you find the tweet, you can browse it on Twitter. The Thoracle platform also shows useful information about the tweet (author analysis, comments and relevant news on tweet content).</p>
      <p>The main goal of the Thoracle project is preventing public manipulation by fake news through social media. It has been developed as a senior-year project by students of Bilkent University.</p>
      <YouTube id='JJmYQQBw-oU' />
      <br />
      <div className='footer-about'>
          <TwitterFollowButton screenName={'thoraclenet'}/>
        <p><i>Thoracle ~ 2020</i></p>
      </div>
    </div>
  );
}
