import React, {  Component } from 'react';
import Image from 'react-bootstrap/Image';
import { LoadState } from '../../../utils/Enums';
import { newsUrl } from '../../../utils/Urls';
import AnalyticsService from "../../firebase/analytics";

let analyticsService = AnalyticsService.getInstance();

export default class RelevantNews extends Component {
  state = {
    loadState: LoadState.LOADING,
    articles: []
  };

  async componentDidMount() {
    try {
      const response = await fetch(`${newsUrl}?tweetUrl=${this.props.tweetUrl}`);
      const responseJson = await response.json();
      this.setState({
        loadState: LoadState.LOADED,
        articles: responseJson.articles
      });
    }
    catch (error) {
      console.error(error);
      this.setState({
        loadState: LoadState.FAILED,
        articles: []
      });
    }
  }

  renderArticle(article) {
    return (
      <div className='article' key={article.url}>
        <a
          className='article-link'
          href={article.url}
          target='_blank'
          rel='noopener noreferrer'
          onClick={() => analyticsService.logEvent(AnalyticsService.Action.article_clicked, [article.url])}
        >
          <Image className='article-image' src={article.urlToImage} thumbnail />
          <div className='article-title'>{article.title}</div>
          <div className='article-author'>{article.author} -&nbsp;</div>
          <div className='article-published-at'>{article.publishedAt}</div>
        </a>
        <br style={{clear: 'both'}} />
        <div className='article-description'>{article.description}</div>
      </div>
    );
  }

  render() {
    let result = '';
    if (this.state.loadState === LoadState.LOADING) {
      result = (<div>Loading relevant news...</div>);
    } else if (this.state.loadState === LoadState.FAILED) {
      result = (<div>Failed to retrieve relevant news. Please try again later.</div>);
    } else if (this.state.loadState === LoadState.LOADED) {
      result = (this.state.articles.map(article => this.renderArticle(article)));
    }
    return (
      <div>
        <h4><span role="img" aria-label="news">ðŸ“°</span> Relevant News</h4>
        <div className='articles'>
          {result}
        </div>
      </div>
    );
  }
}
