import React, { Component, Fragment } from 'react';
import Button from 'react-bootstrap/Button';
import Spinner from 'react-bootstrap/Spinner';
import Badge from 'react-bootstrap/Badge';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Tooltip from 'react-bootstrap/Tooltip';
import { FileDrop } from 'react-file-drop';
import LinkIcon from '@material-ui/icons/Link';
import TwitterIcon from '@material-ui/icons/Twitter';
import CropOriginalIcon from '@material-ui/icons/CropOriginal';
import SearchIcon from '@material-ui/icons/Search';

import { InputType, LoadState } from '../../../utils/Enums';
import { redirect, sleep, extractUrl, getPathFromUrl } from '../../../utils/Commons';
import { tweetSearchUrl, addTweetForumUrl } from '../../../utils/Urls';
import AnalyticsService from "../../firebase/analytics";

let analyticsService = AnalyticsService.getInstance();

export default class HomePage extends Component {
  state = {
    inputType: InputType.IMAGE,
    loadState: LoadState.IDLE,
    fileDraggedOver: false
  };

  async componentDidMount() {
    if (this.props.imageUrl) {
      this.setState({ inputType: InputType.IMAGE_URL });
      await sleep(200);
      const urlInputElement = document.getElementById('image-url-input');
      urlInputElement.value = this.props.imageUrl;
      this.searchByImageUrl();
    }
    if (this.props.tweetUrl) {
      this.setState({ inputType: InputType.TWEET_URL });
      await sleep(200);
      const urlInputElement = document.getElementById('tweet-url-input');
      urlInputElement.value = this.props.tweetUrl;
      this.searchByTweetUrl();
    }
    this.componentDidUpdate();
  }

  componentDidUpdate() {
    const fileInputElement = document.getElementById('image-input');
    if (fileInputElement) {
      fileInputElement.addEventListener('change', () => {
        this.searchByFile(fileInputElement.files[0]);
      });
    }
  }

  changeInputType(inputType) {
    this.setState({ inputType });
  }

  async executeSearch(requestBody) {
    requestBody.append('requested_results', 'search');
    const thoracleApiResponse = await fetch(tweetSearchUrl, {
      method: 'POST',
      body: requestBody
    });
    const thoracleApiResponseJson = await thoracleApiResponse.json();
    const tweetLink = thoracleApiResponseJson.search;
    if (tweetLink) {
      this.setState({ loadState: LoadState.LOADED });
      const redirectUrl = '/tweet?tweetUrl=' + tweetLink + '&found=true';
      analyticsService.logEvent(AnalyticsService.Action.tweet_found, [tweetLink]);
      redirect(redirectUrl);
    } else {
      analyticsService.logEvent(AnalyticsService.Action.tweet_not_found);
      this.setState({ loadState: LoadState.FAILED });
    }
  }

  async searchByFile(file) {
    if (!file.type.startsWith('image/')) {
      alert('Please upload an image file.');
      this.setState({ fileDraggedOver: false });
      return;
    }
    await sleep(200);
    this.setState({ loadState: LoadState.LOADING });
    const requestBody = new FormData();
    requestBody.append('image', file);
    analyticsService.logEvent(AnalyticsService.Action.tweet_ss_send);
    this.executeSearch(requestBody);
  }

  searchByImageUrl() {
    const urlInputElement = document.getElementById('image-url-input');
    const imageUrl = urlInputElement.value;
    this.setState({ loadState: LoadState.LOADING });
    const requestBody = new FormData();
    requestBody.append('image_url', imageUrl);
    analyticsService.logEvent(AnalyticsService.Action.image_link_send, [imageUrl]);
    this.executeSearch(requestBody);
  }

  async searchByTweetUrl() {
    const urlInputElement = document.getElementById('tweet-url-input');
    const urlInput = urlInputElement.value;
    const tweetUrlWithParams = extractUrl(urlInput);
    if (!tweetUrlWithParams) {
      this.setState({ loadState: LoadState.FAILED });
      return;
    }
    const tweetUrl = getPathFromUrl(tweetUrlWithParams);
    analyticsService.logEvent(AnalyticsService.Action.twitter_link_send, [tweetUrl]);
    this.setState({ loadState: LoadState.LOADING });
    await fetch(addTweetForumUrl, {
      method: 'POST',
      body: JSON.stringify({ url: tweetUrl })
    });
    await sleep(500);
    redirect(`/tweet?tweetUrl=${tweetUrl}`);
  }
  
  reset() {
    this.setState({
      loadState: LoadState.IDLE,
      fileDraggedOver: false
    });
  }
  
  getButtonVariant(inputType) {
    const selectedVariant = 'primary';
    const unselectedVariant = 'outline-primary';
    if (this.state.inputType === inputType) {
      return selectedVariant;
    }
    return unselectedVariant;
  }

  renderInputSelection() {
    return (
      <div className='screenshot-input-selection'>
        <Button className='screenshot-input-selection-button'
          variant={this.getButtonVariant(InputType.IMAGE)}
          onClick={() => this.changeInputType(InputType.IMAGE)}
        >
          <CropOriginalIcon /> Upload Image
        </Button>
        &nbsp;
        <Button className='screenshot-input-selection-button'
          variant={this.getButtonVariant(InputType.IMAGE_URL)}
          onClick={() => this.changeInputType(InputType.IMAGE_URL)}
        >
          <LinkIcon /> Paste Image URL
        </Button>
        &nbsp;
        <Button className='screenshot-input-selection-button'
          variant={this.getButtonVariant(InputType.TWEET_URL)}
          onClick={() => this.changeInputType(InputType.TWEET_URL)}
        >
          <TwitterIcon /> Paste Tweet URL
        </Button>
      </div>
    );
  }

  renderDropMessage() {
    if (this.state.fileDraggedOver) {
      return (
        <Fragment>
          <b>Drop Here</b>
        </Fragment>
      );
    }
    return (
      <Fragment>
        <b>Drop a screenshot</b> or <b className='choose-file-link'>choose a file</b>
      </Fragment>
    );
  }

  renderInput() {
    if (this.state.inputType === InputType.IMAGE) {
      return (
        <div className='screenshot-input-file-wrapper'>
          <input className='screenshot-input-file' type='file' id='image-input' accept='image/*'/>
          <label className='screenshot-input-file-label-wrapper' htmlFor='image-input'>
            <FileDrop className='screenshot-input-file-label'
              onDragOver={() => this.setState({ fileDraggedOver: true })}
              onDragLeave={() => this.setState({ fileDraggedOver: false })}
              onDrop={(files) => this.searchByFile(files[0])}
            >
              {this.renderDropMessage()}
            </FileDrop>
          </label>
        </div>
      );
    }
    if (this.state.inputType === InputType.IMAGE_URL) {
      return (
        <div className='screenshot-input-url-wrapper'>
          <div>
            <br/>
            URL of the Tweet Screenshot &nbsp; &nbsp;
            <OverlayTrigger
              placement='left'
              delay={{ show: 100, hide: 100 }}
              overlay={this.renderImageUrlTooltip}
            >
              <Badge variant='secondary'>?</Badge>
            </OverlayTrigger>
            <br/>
            <br/>
            <input className='screenshot-input-url' type='text' id='image-url-input'/>
            <br/>
            <Button
              className='screenshot-search-button'
              variant='dark'
              onClick={() => this.searchByImageUrl()}
            >
              <SearchIcon /> Search
            </Button>
          </div>
        </div>
      );
    }
    if (this.state.inputType === InputType.TWEET_URL) {
      return (
        <div className='screenshot-input-url-wrapper'>
          <div>
            <br/>
            URL of the Tweet &nbsp; &nbsp;
            <OverlayTrigger
                placement='left'
                delay={{ show: 100, hide: 100 }}
                overlay={this.renderTweetUrlTooltip}
            >
              <Badge variant='secondary'>?</Badge>
            </OverlayTrigger>
            <br/>
            <br/>
            <input className='screenshot-input-url' type='text' id='tweet-url-input'/>
            <br/>
            <Button
              className='screenshot-search-button'
              variant='dark'
              onClick={() => this.searchByTweetUrl()}
            >
              
              <SearchIcon /> Search
            </Button>
          </div>
        </div>
      );
    }
  }

  renderTweetUrlTooltip(props) {
    return (
        <Tooltip id='button-tooltip' {...props}>
          Paste the URL of a tweet to see the Thoracle page of it.
        </Tooltip>
    );
  }

  renderImageUrlTooltip(props) {
    return (
      <Tooltip id='button-tooltip' {...props}>
        You can paste the URL of a direct image, Facebook post, Twitter media, or Instagram photo.
      </Tooltip>
    );
  }

  renderLoading(message) {
    return (
      <div className='search-result-wrapper'>
        <br />
        <Spinner animation='border' role='status' size='large'/>
        <br />
        <br />
        {message}
      </div>
    );
  }

  render() {
    let content = '';
    if (this.state.loadState === LoadState.LOADING) {
      content = this.renderLoading('Searching...');
    } else if (this.state.loadState === LoadState.LOADED) {
      content = this.renderLoading('Tweet found! Redirecting...');
    } else if (this.state.loadState === LoadState.FAILED) {
      content = (
        <div className='search-result-wrapper'>
          Tweet could not be found <span role="img" aria-label="sad">ðŸ˜ž</span>
          <br /><br />
          <span role="img" aria-label="warning">âš ï¸</span> The screenshot could be <b>fake</b>
          <br />
          or the tweet might be <b>removed</b>.
          <br /><br />
          <Button variant='dark' onClick={() => this.reset()} >
            Try another one
          </Button>
        </div>
      );
    } else if (this.state.loadState === LoadState.IDLE) {
      content = (
        <Fragment>
          {this.renderInputSelection()}
          {this.renderInput()}
        </Fragment>
      );
    }

    return (
      <Fragment>
        <h1 className='header-home'>Tweet Search <div className='by-thoracle'>by <i>Thoracle</i></div></h1>
        {content}
      </Fragment>
    );
  }
}
