import React, { Component } from 'react';
import Spinner from 'react-bootstrap/Spinner'
import { TwitterTweetEmbed } from 'react-twitter-embed';
import { LoadState } from '../../../utils/Enums';
import { getTweetId, redirect } from '../../../utils/Commons';
import { popularTweetsUrl } from '../../../utils/Urls';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import AnalyticsService from "../../firebase/analytics";

let analyticsService = AnalyticsService.getInstance();

export default class PopularTweets extends Component {
  state = {
    loadState: LoadState.LOADING,
    popularTweets: []
  }

  async componentDidMount() {
    try {
      const response = await fetch(popularTweetsUrl)
      const responseJson = await response.json();
      this.setState({
        loadState: LoadState.LOADED,
        popularTweets: responseJson
      });
    }
    catch (error) {
      console.error(error);
      this.setState({
        loadState: LoadState.FAILED,
        popularTweets: []
      });
    }
  }

  getTweetWrapperId(tweetId) {
    return `tweet-wrapper-${tweetId}`;
  }

  onHoverTweet(tweetId) {
    const wrapperElement = document.getElementById(this.getTweetWrapperId(tweetId));
    wrapperElement.classList.add('tweet-wrapper-hovered');
  }

  onUnhoverTweet(tweetId) {
    const wrapperElement = document.getElementById(this.getTweetWrapperId(tweetId));
    wrapperElement.classList.remove('tweet-wrapper-hovered');
  }

  render() {
    let result = '';
    if (this.state.loadState === LoadState.LOADING) {
      result = (
        <div>
          <br />
          <Spinner animation='border' role='status' size='large'/>
        </div>
      );
    } else if (this.state.loadState === LoadState.FAILED) {
      result = (<div>Failed to retrieve popular tweets. Please try again later.</div>);
    } else if (this.state.loadState === LoadState.LOADED) {
      result = (this.state.popularTweets.slice(0, 10).map(tweet => {
        const tweetId = getTweetId(tweet.url);
        const thoracleUrl = '/tweet?tweetUrl=' + tweet.url;
        return (
          <Row key={tweetId}>
            <Col xs="12" className='tweet-wrapper' id={this.getTweetWrapperId(tweetId)}>
              <TwitterTweetEmbed
                tweetId={tweetId}
                options={{conversation: 'none'}}
              />
              <div
                className='tweet-redirect'
                onClick={() => {
                  analyticsService.logEvent(AnalyticsService.Action.popular_tweet_clicked, [tweet.url]);
                  redirect(thoracleUrl);
                }}
                onMouseEnter={() => this.onHoverTweet(tweetId)}
                onMouseLeave={() => this.onUnhoverTweet(tweetId)}
              />
            </Col>
          </Row>
        );
      }));
    }
    return (
      <div className='popular-tweets-wrapper'>
        <h3><span role="img" aria-label="trending">ðŸ”¥</span> Trending on Thoracle</h3>
        {result}
      </div>
    );
  }
}
