import React, {  Component } from 'react';
import Disqus from "disqus-react";
import "../../../styles/App.css"
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import Button from '@material-ui/core/Button';
import Row from "react-bootstrap/Row";
import AnalyticsService from "../../firebase/analytics";

let analyticsService = AnalyticsService.getInstance();

export default class Comments extends Component {
    state = {
        displayingComments: false
    }

    componentDidMount() {
        this.componentDidUpdate();
    }

    commentCountText = '0 Comments';
    componentDidUpdate() {
        setTimeout(() => {
            const commentCountElement = document.getElementById('comment-count-display');
            if (commentCountElement) {
                if (commentCountElement.innerText !== '') {
                    this.commentCountText = commentCountElement.innerText;
                } else {
                    commentCountElement.innerText = this.commentCountText;
                }
            }
        }, 1000);
    }

    renderExpandComments(disqusConfig) {
        analyticsService.logEvent(AnalyticsService.Action.open_comments, [this.props.pageUrl]);
        const numComments = <Disqus.CommentCount
            shortname='app-thoracle-site'
            config={disqusConfig}
        />

        return (
            <div className='show-comments-button-wrapper'>
                <Button
                    onClick={() => { this.setState({ displayingComments: true}) }}
                    endIcon={<ExpandMoreIcon/>}>
                    <div className='show-comments-button-label'>
                    <span role="img" aria-label="comments">ðŸ’¬</span>&nbsp;
                    <span id='comment-count-display'>{numComments}</span>
                    </div>
                </Button>
            </div>
        )
    }

    renderCollapseComments() {
        analyticsService.logEvent(AnalyticsService.Action.close_comments, [this.props.pageUrl]);
        return (
            <div className='show-comments-button-wrapper'>
                <Button
                    onClick={() => { this.setState({ displayingComments: false}) }}
                    endIcon={<ExpandLessIcon/>}>
                    <div className='show-comments-button-label'>
                        <span role="img" aria-label="comments">ðŸ’¬</span>&nbsp;
                        Hide Comments
                    </div>
                </Button>
            </div>
        );
    }

    render() {
        const disqusShortname = "app-thoracle-site"
        let disqusConfig = {
            url: this.props.pageUrl,
            identifier: this.props.disqusId,
            title: this.props.disqusId
        }

        return (
            <div>
                <Row>
                    {this.state.displayingComments ? this.renderCollapseComments() : this.renderExpandComments(disqusConfig)}
                </Row>
                <div>
                    {this.state.displayingComments && <Disqus.DiscussionEmbed
                        shortname={disqusShortname}
                        config={disqusConfig}
                    /> }
                </div>
            </div>
        )
    }
}
